import { WISHLIST_FRAGMENT } from '@/wishlist/api/fragments';

export const ADD_PRODUCTS_TO_WISHLIST_MUTATION = `
  mutation ADD_PRODUCTS_TO_WISHLIST_MUTATION(
      $wishlistId: ID!, 
      $wishlistItems: [WishlistItemInput!]!,
    ) {
    addProductsToWishlist(
      wishlistId: $wishlistId
      wishlistItems: $wishlistItems
    ) {
      wishlist {
        ...WISHLIST_FRAGMENT
      }
      user_errors {
        code
        message
      }
    }
  }
${WISHLIST_FRAGMENT}
`;
