import { Wishlist, Item, Option } from '@/wishlist/data/models/wishlist';

export function transformWishlist(
  data: any,
  enteredOptions?: { uid: string; value: string }[]
): Wishlist | null {
  if (!data) return null;

  return {
    id: data.id,
    updated_at: data.updated_at,
    sharing_code: data.sharing_code,
    items_count: data.items_count,
    items: transformItems(data, enteredOptions ?? []),
  };
}

function transformItems(
  data: any,
  enteredOptions: { uid: string; value: string }[]
): Item[] {
  if (!data?.items_v2?.items?.length) return [];

  return data.items_v2.items.map((item: any) => {
    const selectedOptions = getSelectedOptions(item);
    return {
      id: item.id,
      quantity: item.quantity,
      description: item.description,
      added_at: item.added_at,
      enteredOptions,
      selectedOptions,
      product: { sku: item.product.sku },
    };
  });
}

function getSelectedOptions(item: Item): Option[] {
  if (item.__typename === 'ConfigurableWishlistItem') {
    return item.configurable_options
      ? item.configurable_options.map((option: any) => ({
          uid: option.configurable_product_option_value_uid,
        }))
      : [];
  }
  return [];
}
