import { Initializer } from '@adobe-commerce/elsie/lib';
import { Lang } from '@adobe-commerce/elsie/i18n';
import { events } from '@adobe-commerce/event-bus';
import { state } from '@/wishlist/lib/state';
import {
  initializeWishlist,
  resetWishlist,
  mergeWishlists,
} from '@/wishlist/api';
import { setPersistedWishlistData } from '@/wishlist/lib/persisted-data';

type ConfigProps = {
  langDefinitions?: Lang;
  isGuestWishlistEnabled?: boolean;
};

export const initialize = new Initializer<ConfigProps>({
  init: async (config) => {
    const defaultConfig = { isGuestWishlistEnabled: false, ...config };

    initialize.config.setConfig(defaultConfig);
    initializeWishlist().catch(console.error);
  },

  listeners: () => [
    events.on(
      'wishlist/data',
      (payload) => {
        setPersistedWishlistData(payload);
      },
      { eager: true }
    ),
    events.on(
      'authenticated',
      async (authenticated) => {
        if (state.authenticated && !authenticated) {
          // Reset wishlist if the user is no longer authenticated
          events.emit('wishlist/reset', undefined);
        }
        // Login event occurred
        if (authenticated && !state.authenticated) {
          state.authenticated = authenticated;
          const wishlist = await initializeWishlist().catch(console.error);
          if (wishlist) {
            mergeWishlists(wishlist);
          }
        }
      },
      { eager: true }
    ),
    events.on('wishlist/reset', () => {
      resetWishlist().catch(console.error);
      events.emit('wishlist/data', null);
    }),
  ],
});

export const config = initialize.config;
