import { Wishlist, Item } from '@/wishlist/data/models';
import { addProductsToWishlist } from '@/wishlist/api';
import {
  getPersistedWishlistData,
  clearPersistedLocalStorage,
} from '@/wishlist/lib/persisted-data';
import { isMatchingWishlistItem } from '@/wishlist/lib/wishlist-item-comparator';

export const mergeWishlists = async (
  wishlist: Wishlist
): Promise<Wishlist | null> => {
  if (!wishlist) {
    return null;
  }

  const guestWishlist: {
    id?: string;
    items?: Item[];
  } = getPersistedWishlistData(true);
  const itemsToMerge: any[] = [];

  guestWishlist?.items?.forEach((item: any) => {
    const optionUIDs =
      item.selectedOptions?.map((option: any) => option.uid) || [];

    const exists = wishlist.items.some((wishlistItem: any) =>
      isMatchingWishlistItem(wishlistItem, {
        sku: item.product.sku,
        optionUIDs,
      })
    );

    if (!exists) {
      const mergeItem = {
        sku: item.product.sku,
        quantity: 1,
        optionsUIDs: optionUIDs,
        enteredOptions: item.enteredOptions || undefined,
      };

      itemsToMerge.push(mergeItem);
    }
  });

  if (itemsToMerge.length === 0) {
    return null;
  }

  const result = await addProductsToWishlist(itemsToMerge);
  clearPersistedLocalStorage();
  return result;
};
