import { CUSTOMIZABLE_OPTIONS_FRAGMENT } from './CustomizableOptionsFragment.graphql';

const CONFIGURABLE_WISHLIST_ITEM_FRAGMENT = `
  ... on ConfigurableWishlistItem {
    configurable_options {
      option_label
      value_label
      configurable_product_option_value_uid
      configurable_product_option_uid
    }
    configured_variant {
      canonical_url
    }
  }
`;

const DOWNLOADABLE_WISHLIST_ITEM_FRAGMENT = `
  ... on DownloadableWishlistItem {
    added_at
    description
    links_v2 {
      sample_url
      sort_order
      title
      uid
    }
    quantity
  }
`;

const GIFT_CARD_WISHLIST_ITEM_FRAGMENT = `
  ... on GiftCardWishlistItem {
    added_at
    description
    gift_card_options {
      amount {
        value
        currency
      }
      custom_giftcard_amount {
        value
        currency
      }
      message
      recipient_email
      recipient_name
      sender_email
      sender_name
    }
  }
`;

const BUNDLE_WISHLIST_ITEM_FRAGMENT = `
  ... on BundleWishlistItem {
    bundle_options {
      label
      type
      uid
      values {
        uid
        label
        quantity
      }
    }
  }
`;

export const WISHLIST_ITEM_FRAGMENT = `
fragment WISHLIST_ITEM_FRAGMENT on WishlistItemInterface {
    __typename
    id
    quantity
    description
    added_at
    product {
      sku
    }
    ${CONFIGURABLE_WISHLIST_ITEM_FRAGMENT}
    ${DOWNLOADABLE_WISHLIST_ITEM_FRAGMENT}
    ${GIFT_CARD_WISHLIST_ITEM_FRAGMENT}
    ${BUNDLE_WISHLIST_ITEM_FRAGMENT}
    customizable_options {
      ...CUSTOMIZABLE_OPTIONS_FRAGMENT
    }
  }
  
  ${CUSTOMIZABLE_OPTIONS_FRAGMENT}
`;
