import { fetchGraphQl } from '@/wishlist/api';
import { StoreConfigModel } from '@/wishlist/data/models';
import { transformStoreConfig } from '@/wishlist/data/transforms';
import { handleFetchError } from '@/wishlist/lib/fetch-error';

import { STORE_CONFIG_QUERY } from './graphql/StoreConfigQuery';

export const getStoreConfig = async (): Promise<StoreConfigModel | null> => {
  return fetchGraphQl(STORE_CONFIG_QUERY, {
    method: 'GET',
    cache: 'force-cache',
  }).then(({ errors, data }) => {
    if (errors) return handleFetchError(errors);

    return transformStoreConfig(data);
  });
};
