import { events } from '@adobe-commerce/event-bus';
import { state } from '@/wishlist/lib/state';
import { getStoreConfig, getWishlists } from '@/wishlist/api';
import { getPersistedWishlistData } from '@/wishlist/lib/persisted-data';
import { Wishlist } from '@/wishlist/data/models';

export const initializeWishlist = async (): Promise<Wishlist | null> => {
  if (state.initializing) return null;

  state.initializing = true;

  // set config
  if (!state.config) {
    state.config = await getStoreConfig();
  }

  const payload = state.authenticated
    ? await getDefaultWishlist()
    : await getGuestWishlist();

  events.emit('wishlist/initialized', payload);
  events.emit('wishlist/data', payload);

  state.initializing = false;

  return payload;
};

export async function getDefaultWishlist() {
  const wishlists = await getWishlists();
  const wishlist = wishlists ? wishlists[0] : null;
  if (!wishlist) return null;

  state.wishlistId = wishlist.id;

  return wishlist;
}

export async function getGuestWishlist() {
  try {
    return await getPersistedWishlistData();
  } catch (error) {
    console.error(error);
    throw error;
  }
}
