import { fetchGraphQl, getPersistedWishlistData } from '@/wishlist/api';
import { state } from '@/wishlist/lib/state';
import { handleFetchError } from '@/wishlist/lib/fetch-error';
import { ADD_PRODUCTS_TO_WISHLIST_MUTATION } from './graphql/addProductsToWishlistMutation';
import { Wishlist } from '@/wishlist/data/models/wishlist';
import { transformWishlist } from '@/wishlist/data/transforms';
import { events } from '@adobe-commerce/event-bus';
import { isMatchingWishlistItem } from '@/wishlist/lib/wishlist-item-comparator';

export const addProductsToWishlist = async (
  items: [
    {
      sku: string;
      quantity: number;
      optionsUIDs?: string[]; // should be the options available for the product
      enteredOptions?: { uid: string; value: string }[]; // refer to both selected and entered options
    }
  ]
): Promise<Wishlist | null> => {
  if (!items) return null;

  const wishlist = getPersistedWishlistData();
  let updatedWishlist = {
    id: wishlist?.id ?? '',
    updated_at: '',
    sharing_code: '',
    items_count: 0,
    items: wishlist?.items ?? [],
  };

  for (const item of items) {
    const skuExists = updatedWishlist.items?.some((wishlistItem: any) =>
      isMatchingWishlistItem(wishlistItem, {
        sku: item.sku,
        optionUIDs: item.optionsUIDs,
      })
    );

    if (skuExists) {
      continue;
    }

    const selectedOptions: { uid: string }[] = item.optionsUIDs
      ? item.optionsUIDs?.map((option: any) => ({
          uid: option,
        }))
      : [];

    updatedWishlist.items = [
      ...updatedWishlist.items,
      {
        id: crypto.randomUUID(),
        quantity: item.quantity,
        selectedOptions,
        enteredOptions: item.enteredOptions ?? [],
        product: { sku: item.sku },
      },
    ];
  }

  updatedWishlist.items_count = updatedWishlist.items?.length;
  events.emit('wishlist/data', updatedWishlist);

  if (state.authenticated) {
    if (!state.wishlistId) {
      events.emit('wishlist/data', wishlist);
      throw Error('Wishlist ID is not set');
    }

    const variables = {
      wishlistId: state.wishlistId,
      wishlistItems: items.map(
        ({ sku, quantity, optionsUIDs, enteredOptions }) => ({
          sku,
          quantity,
          selected_options: optionsUIDs,
          entered_options: enteredOptions,
        })
      ),
    };

    const { errors, data }: { errors: any; data: any } = await fetchGraphQl(
      ADD_PRODUCTS_TO_WISHLIST_MUTATION,
      { variables }
    );

    const _errors = [
      ...(data?.addProductsToWishlist?.user_errors ?? []),
      ...(errors ?? []),
    ];

    // if saving item to wishlist for logged user failed, we need to restore original wishlist in storage:
    if (_errors.length > 0) {
      events.emit('wishlist/data', wishlist);
      return handleFetchError(_errors);
    }

    const updatedWishlist = transformWishlist(
      data.addProductsToWishlist.wishlist,
      items[0]?.enteredOptions ?? []
    );
    events.emit('wishlist/data', updatedWishlist);
  }

  return null;
};
