import { state } from '@/wishlist/lib/state';
import { Wishlist } from '@/wishlist/data/models';

export const resetWishlist = (): Promise<Wishlist | null> => {
  state.wishlistId = null;
  state.authenticated = false;

  return Promise.resolve(null);
};
